.text90{
  font-size: 5.625em;
}

.text45{
  font-size: 2.8125em;
  line-height: 110%;
}

.text28{
  font-size: 1.75em;
}

.text18{
  font-size: 1.125em;
}

.text16{
  font-size: 1em;
  line-height: 110%;
}

.text15{
  font-size: .9375em;
  letter-spacing: -0.01875em;
}

.text14{
  font-size: .875em;
  line-height: 120%;
}

.bold-text{
  font-weight: 700;
}

.medium-text{
  font-weight: 500;
}


@media screen and (max-width: 992px){
  .text90{
    font-size: 3em;
  }

  .text45{
    font-size: 1.5em;
  }

  .text28{
    font-size: 1em;
  }

  .text18,
  .text16,
  .text15,
  .text14{
    font-size: .875em;
  }
}


@media screen and (max-width: 576px) {
  .text90 {
    font-size: 2.5em;
  }
}
