.footer{
  padding: 3.75em 0 8.75em;
  .container{
    max-width: 1440px;
  }
  &__inner{
    padding-bottom: 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid #E3EEFF;
  }
  &__logo{
    width: 26.44em;
    flex-shrink: 0;
    display: block;
    img{
      width: 100%;
      display: block;
    }
  }
  &__bottom{
    display: flex;
    justify-content: space-between;
    margin-top: 3.5em;
    .text16{
      line-height: 150%;
    }
  }
  &__conf{
    text-decoration: underline;
  }
}

.persons{
  margin-top: 2.5em;
  display: flex;
  align-items: flex-start;
  &__item{
    max-width: 24.5em;
    @include grid100gap(.5em);
    @include lastM(0 2.5em 0 0);
    a{
      color: $primary;
      &:hover{
        text-decoration: underline !important;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    padding: 1.875em 0 3.25em;
    &__inner{
      padding-bottom: 1.25em;
    }
    &__logo{
      width: 14.2em;
    }
    &__bottom{
      margin-top: 2.25em;
    }
  }

  .footer-contacts{
    &__link{
      span{
        font-size: .875em;
      }
    }
  }

  .persons{
    margin-top: 1.25em;
    &__item{
      max-width: 15.5em;
      @include grid100gap(.25em);
      @include lastM(0 1.25em 0 0);
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__top{
      padding-bottom: 3.25em;
      position: relative;
    }
    &__logo{
      width: 10.5em;
    }
    &__bottom{
      flex-direction: column;
      .text16{
        line-height: 120%;
      }
    }
    &__c{
      flex-shrink: 0;
    }
    &__conf{
      margin-top: 1em;
    }
  }

  .footer-contacts{
    position: absolute;
    bottom: 0;
    left: 0;
    &__link{
      display: flex;
      align-items: center;
    }
    .socials{
      order: -1;
      margin: 0 .375em 0 0;
    }
  }

  .persons{
    display: block;
    &__item{
      @include lastM(0 0 1em);
    }
  }
}
