.fg {
  position: relative;
  margin: 0;
  input {
    border: 1px solid #fff;
    padding: 0 1.875em;
    height: 4em;
    width: 100%;
    font-size: 1em;
    font-weight: 700;
    background-color: #fff;
    color: $primary2;
    border-radius: 1.875em;
    &::placeholder {
      color: $primary2;
    }
    &:focus{
      border-color: $primary2;
    }
  }
  .iti{
    width: 100%;
    &__selected-flag{
      padding: 0 .75em 0 1.875em;
    }
    &__flag{
      box-shadow: unset;
      &-container{
        &:hover .iti__selected-flag{
          background-color: transparent;
        }
      }
    }
    &__dial-code,
    &__preferred,
    &__divider{
      display: none;
    }
    &__arrow{
      border: none;
      background-image: url(../img/arrow2.svg);
      width: 0.8125em;
      height: 0.625em;
      @include bgImgSize(100%);
    }
    &__country{
      padding: 0;
      color: $primary2;
      font-size: 1em;
      @include lastM(0 0 .75em);
      &.iti__highlight{
        background-color: transparent;
      }
      &:hover{
        background-color: transparent;
        opacity: .8;
      }
      .iti__flag-box{
        display: none;
      }
      &-list{
        padding: 1.25em .75em 1.25em 1.25em;
        border-radius: 1em;
        background: #fff;
        box-shadow: 0 .1875em 5.1875em 0 rgba(0, 0, 0, 0.10);
        border: none;
        margin-top: .25em;
        @include scroll;
      }
    }
  }
  .international-phone{
    padding-left: 5.31em !important;
    font-weight: 400;
  }
}

@media screen and (max-width: 576px){
  .fg{
    input{
      height: 3.375em;
    }
  }
}
