.header{
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  padding: 1.5em 0;
  transition: all .3s;
  background: #fff;
  &._transform{
    //transform: translateY(-100%);
    //background: #fff;
  }
  &._fix{
    //position: fixed;
    transform: translateY(-0%);
    box-shadow: 0 .25em .25em 0 rgba(0,0,0,.1);
  }
  .container{
    max-width: 1440px;
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 16.875em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.menu{
  margin-left: 5em;
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__bg{
    display: none;
  }
}

.footer-contacts,
.header-contacts,
.nav{
  &__link{
    font-weight: 700;
    color: $primary;
    &:hover{
      color: $green;
    }
    span{
      font-size: 1em;
      line-height: 120%;
    }
  }
}

.header-contacts{
  &__link{
    span{
      font-size: 1.25em;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    @include lastM(0 2.5em 0 0);
  }
}

.footer-contacts,
.header-contacts,
.socials{
  display: flex;
  align-items: center;
}

.socials{
  margin-left: .375em;
  &__item{
    width: 2.5em;
    display: flex;
    align-items: center;
    @include lastM(0 .375em 0 0);
    svg{
      width: 100%;
      height: auto;
    }
    &:hover{
      path{
        fill: $green;
      }
    }
  }
}

.burger{
  display: none;
}

@media screen and (max-width: 992px){
  .header{
    padding: 1.25em 0;
    &__logo{
      width: 9em;
    }
  }

  .menu{
    margin-left: 2.67em;
  }

  .header-contacts{
    &__link{
      span{
        font-size: .875em;
      }
    }
  }

  .nav{
    &__link{
      @include lastM(0 1.5em 0 0);
      span{
        font-size: .875em;
      }
    }
  }

  .socials{
    &__item{
      width: 1.75em;
      @include lastM(0 .125em 0 0);
    }
  }
}


@media screen and (max-width: 576px) {
  .header {
    &__logo {
      width: 10.5em;
    }
    &._open{
      .burger{
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
      .menu{
        opacity: 1;
        pointer-events: unset;
        &__bg{
          display: block;
        }
      }
    }
  }

  .menu{
    position: absolute;
    top: 100%;
    right: 1.25em;
    display: block;
    padding: 1.25em;
    z-index: 21;
    background-color: #fff;
    border-radius: 1em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
    &__bg{
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: $vh;
      background: rgba(0, 0, 0, 0.50);
      z-index: 20;
    }
  }

  .header-contacts,
  .nav{
    flex-direction: column;
    align-items: flex-start;
    &__link{
      span{
        line-height: 100%;
      }
    }
  }

  .nav{
    &__link{
      @include lastM(0 0 .5em);
    }
  }

  .header-contacts{
    margin-top: 1.25em;
  }

  .socials{
    margin: .375em 0 0;
  }

  .burger{
    z-index: 22;
    width: 1.5em;
    height: 1.5em;
    @include center;
    position: relative;
    img{
      width: 100%;
      @include centerAbsolute;
    }
    ._view2{
      opacity: 0;
    }
  }
}
