._full-bg{
  position: relative;
  &:before{
    content: '';
    z-index: -1;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    height: 100%;
    width: calc(100% - 3.75em);
    background-size: cover;
    background-position: center;
    border-radius: 2.5em;
    background-color: $black;
  }
}

.main{
  padding: 6.25em 0;
  color: #fff;
  &:before{
    background-image: url(../img/main-bg.webp);
  }
  &__subtitle{
    display: flex;
    align-items: center;
    margin-bottom: 1.5em;
    img{
      width: 1.25em;
      flex-shrink: 0;
      margin-right: .5em;
    }
  }
  &__nav{
    margin-top: 3.5em;
    display: flex;
    .m-btn{
      @include lastM(0 .625em 0 0);
    }
  }
  &__items{
    margin-top: 6.25em;
    display: grid;
    grid-template-columns: 1.5fr 1.14fr .96fr;
    gap: 1.25em 1.25em;
  }
}

.main-item{
  display: flex;
  align-items: center;
  &__icon{
    width: 3.25em;
    display: block;
    flex-shrink: 0;
    margin-right: .75em;
  }
  .text16{
    letter-spacing: -0.02em;
  }
}

.desc{
  padding: 8.75em 0 3.75em;
  &__inner{
    display: grid;
    grid-template-columns: calc(100% - 25.375em - 1.81em) 25.375em;
    justify-content: space-between;
    width: calc(100% + 2.25em);
    align-items: center;
  }
  &__content{
    @include grid100gap(2.25em);
  }
  &__title{
    line-height: 120%;
    span{
      color: $primary2;
    }
  }
  &__text{
    line-height: 130%;
  }
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
}

.programs{
  padding: 3.75em 0;
  &__top{
    display: flex;
    align-items: center;
  }
  &__badges{
    display: flex;
    align-items: center;
    margin-left: 1.25em;
  }
  &__items{
    margin-top: 3.5em;
    @include grid100gap(1.25em);
  }
}

.m-badge{
  display: flex;
  align-items: center;
  padding: 0 1em;
  height: 2.4375em;
  border-radius: 2.5em;
  background: #E3EEFF;
  font-weight: 500;
  margin-right: .5em;
  span{
    font-size: 1em;
    line-height: 120%;
  }
}

.program-card{
  padding: 2.5em 5em;
  border-radius: 2.5em;
  background: #EEF2F9;
  position: relative;
  min-height: 3em;
  &__content{
    max-width: 28em;
  }
  &__nav{
    display: flex;
    margin-top: 1.875em;
    .m-btn{
      ._view2{
        display: none;
      }
    }
  }
  &__img{
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    img{
      height: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__items{
    margin-top: 1.5em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 1.5em;
  }
  &__groups{
    display: flex;
    justify-content: space-between;
    .program-item{
      margin-top: 1.75em;
      max-width: 11em;
    }
  }
}

.program-item{
  display: flex;
  align-items: center;
  &._big{
    grid-column: span 2;
    max-width: 12em;
  }
  &__num{
    margin-right: .5em;
    min-width: 1.625em;
    font-weight: 500;
    color: $primary2;
    flex-shrink: 0;
    span{
      font-size: 2.8125em;
    }
    &._green{
      color: #00A94F;
    }
    &._red{
      color: #ED1D24;
    }
  }
  &__title{
    .text18{
      letter-spacing: -0.0225em;
    }
  }
}

.benefit{
  padding: 3.75em 0;
  &__title{
    padding: 0 1em;
    text-align: center;
    span{
      color: $green;
    }
  }
  &__items{
    margin-top: 4.75em;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 5em / 3));
    gap: 2.5em;
    align-items: flex-start;
  }
  &__nav{
    display: flex;
    justify-content: center;
    margin-top: 4.75em;
  }
}

.benefit-item{
  position: relative;
  padding-left: 3.25em;
  @include grid100gap(.625em);
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 2.5em;
    height: 2.8125em;
    background-image: url(../img/check.webp);
    @include bgImgSize(100%);
  }
  &__title,
  &__desc{
    font-size: 1em;
    letter-spacing: -0.02em;
  }
  &__title{
    font-weight: 500;
  }
}

.practice{
  overflow: hidden;
  margin: 3.75em 0;
  padding: 6.25em 0 0;
  color: #fff;
  &:before{
    background-image: url(../img/practice-bg.webp);
  }
  &__items{
    margin-top: 3.5em;
    display: grid;
    grid-template-columns: 1fr 1.25fr 1fr 1fr;
    gap: 1.25em 3.5em;
    align-items: flex-start;
    ._big{
      grid-column: span 2;
    }
  }
  &__bottom{
    width: calc(100% + 6em);
    display: grid;
    grid-template-columns: calc(100% - 35.4375em) 35.4375em;
  }
  &__img{
    margin-top: -3.75em;
    img{
      width: 100%;
      display: block;
    }
  }
}

.practice-item{
  position: relative;
  padding-left: 2.8125em;
  @include grid100gap(.75em);
  &:before{
    content: '';
    position: absolute;
    width: 1.875em;
    height: 1.875em;
    left: 0;
    top: 0;
    background-image: url(../img/point2.svg);
    @include bgImgSize(100%);
  }
  .text28{
    font-weight: 500;
    letter-spacing: -0.03563em;
  }
  .text16{
    line-height: 100%;
    letter-spacing: -0.02em;
  }
}

.practice-places{
  padding-top: 7.76em;
}

.practice-slider{
  padding: 2.5em 3.375em 2.5em 0;
  max-width: 36.625em;
  position: relative;
  &__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1em);
    }
    &-slide{
      width: 20%;
      padding-right: 1em;
    }
    &-button{
      top: 50%;
      transform: translateY(-50%);
      margin: 0;
      width: 2.125em;
      height: auto;
      @include center;
      opacity: .5;
      border-radius: 50%;
      filter: drop-shadow(0 0 2.3125em rgba(0, 0, 0, 0.30));
      img{
        width: 100%;
        display: block;
      }
      &:hover{
        opacity: .2;
      }
      &-next{
        right: 0;
      }
    }
  }
}

.practice-card{
  img{
    width: 100%;
    display: block;
    border-radius: 2.5em;
  }
}


@media screen and (max-width: 992px){
  ._full-bg{
    &:before{
      width: calc(100% - 2em);
      border-radius: 1.25em;
    }
  }

  .main{
    padding: 2.5em 0 3.06em;
    &:before{
      background-image: url(../img/main-bg2.webp);
    }
    &__subtitle{
      margin-bottom: 1em;
      img{
        width: .75em;
        margin-right: .25em;
      }
    }
    &__nav{
      margin-top: 1.75em;
    }
    &__items{
      margin-top: 3em;
      grid-template-columns: 100%;
      max-width: 19em;
    }
  }

  .main-item{
    font-weight: 500;
    &__icon{
      width: 2.125em;
      margin-right: .25em;
    }
    .text16{
      letter-spacing: -0.0125em;
    }
  }

  .desc{
    padding: 3.75em 0 1.875em;
    &__inner{
      grid-template-columns: calc(100% - 14.375em) 14.375em;
      width: calc(100% + 1.5em);
    }
    &__content{
      gap: 1em;
    }
    &__text{
      max-width: 33em;
    }
  }

  .programs{
    padding: 1.875em 0;
    &__badges{
      margin-left: .625em;
    }
    &__items{
      margin-top: 1.25em;
      gap: .625em;
    }
  }

  .m-badge{
    margin-right: .375em;
    height: 1.4375em;
    border-radius: 1.25em;
    padding: 0 .5em;
    span{
      font-size: .875em;
    }
  }

  .program-card{
    padding: 1.25em 2.5em;
    min-height: 7em;
    border-radius: 1.3125em;
    &__nav{
      margin-top: .9375em;
      .m-btn{
        ._view1{
          display: none;
        }
        ._view2{
          display: block;
        }
      }
    }
    &__content{
      max-width: 14.5em;
    }
    &__items{
      grid-template-columns: repeat(3,calc(100% / 3 - .5em));
      gap: .75em;
      margin-top: .75em;
    }
    &__group{
      @include lastM(0 1.5em 0 0);
      .program-item{
        margin-top: .75em;
      }
    }
    &._type2{
      min-height: unset;
    }
  }

  .program-item{
    &._big{
      max-width: 6em;
    }
    &__num{
      margin-right: .25em;
      min-width: .875em;
      span{
        font-size: 1.5em;
      }
    }
  }

  .benefit{
    padding: 1.875em 0;
    &__title{
      padding: 0 3em;
    }
    &__items{
      margin-top: 2.25em;
      grid-template-columns: repeat(2,calc(50% - 1.25em));
      gap: 1.5em 2.5em;
    }
    &__nav{
      margin-top: 2.25em;
    }
  }

  .benefit-item{
    gap: .375em;
    padding-left: 1.75em;
    &:before{
      width: 1.375em;
      height: 1.5em;
    }
    &__title,
    &__desc{
      font-size: .875em;
      letter-spacing: -0.0125em;
    }
  }

  .practice{
    margin: 1.875em 0;
    padding: 2.5em 0 0;
    &:before{
      background-image: url(../img/practice-bg2.webp);
    }
    &__items{
      margin-top: 2.25em;
      gap: 1.75em 1.25em;
      grid-template-columns: 1fr 1.25fr 1.25fr 1fr;
    }
    &__bottom{
      width: calc(100% + 1.875em);
      grid-template-columns: calc(100% - 18.5em) 18em;
      gap: .5em;
    }
    &__img{
      margin-top: -3em;
    }
  }

  .practice-item{
    gap: .5em;
    padding-left: 1.375em;
    &:before{
      width: 1em;
      height: 1em;
    }
  }

  .practice-places{
    padding-top: 3.5em;
  }

  .practice-slider{
    padding: 1.25em 2.5em 1.25em 0;
    max-width: 21em;
    .swiper{
      &-container{
        width: calc(100% + .5em);
      }
      &-slide{
        padding-right: .5em;
      }
      &-button{
        width: 1.5em;
      }
    }
  }

  .practice-card{
    img{
      border-radius: 1.25em;
    }
  }
}


@media screen and (max-width: 576px){
  ._full-bg{
    &:before{
      width: calc(100% - 1.25em);
    }
  }

  .main{
    &:before{
      background-image: url(../img/main-bg3.webp);
    }
    &__subtitle{
      margin-bottom: .75em;
    }
    &__nav{
      margin-top: 1.5em;
    }
    &__items{
      margin-top: 5em;
    }
  }

  .desc{
    &__inner{
      grid-template-columns: 100%;
      width: 100%;
      gap: 2.5em;
    }
    &__content{
      gap: 1.125em;
    }
    &__img{
      padding: 0 1.375em;
    }
    &__text{
      letter-spacing: -0.00625em;
    }
  }

  .programs{
    &__top{
      display: block;
    }
    &__badges{
      margin: .75em 0 0;
    }
  }

  .program-card{
    padding: 1.25em;
    min-height: 8.5625em;
    &__nav{
      margin-top: 1.25em;
    }
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
    &__groups{
      display: block;
    }
    &__group{
      max-width: 6em;
      @include lastM(0 0 1em);
    }
  }

  .program-item{
    .text18{
      font-size: .625em;
    }
  }

  .benefit{
    &__title{
      padding: 0 1em;
      span{
        display: block;
        line-height: inherit;
      }
    }
    &__items{
      grid-template-columns: 100%;
      padding: 0 .44em 0 1em;
    }
  }

  .practice{
    &:before{
      background-image: url(../img/practice-bg3.webp);
    }
    &__items{
      grid-template-columns: 100%;
      ._big{
        grid-column: 1/-1;
      }
    }
    &__bottom{
      width: 100%;
      grid-template-columns: 100%;
      gap: 0;
    }
    &__img{
      margin-top: 0;
    }
  }

  .practice-slider{
    padding: 1.25em 2.5em 1.25em 0;
    max-width: 16.5em;
    .swiper{
      &-slide{
        width: 25%;
      }
    }
  }
}
