.career{
  padding: 3.75em 0;
  &__top{
    max-width: 49.5em;
    @include grid100gap(1.5em);
  }
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: 2.25em;
    width: calc(100% + .75em);
    .m-badge{
      margin: .75em .75em 0 0;
      font-weight: 400;
      span{
        letter-spacing: -0.02em;
      }
    }
  }
  &__nav{
    margin-top: 3.125em;
    display: flex;
  }
}

.exams{
  padding: 3.75em 0;
  &__grid{
    padding: 3.5em 2.81em 0;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
  }
}

.exam-card,
.review-card{
  &:after,
  &:before{
    content: '';
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
  }
  &:after{
    border-radius: 2.5em;
    background: #E3EEFF;
    height: calc(100% - 3.44em);
    z-index: -1;
  }
  &:before{
    height: 100%;
    background: rgba(255, 255, 255, 0.30);
    backdrop-filter: blur(97.49999237060547px);
  }
}

.exam-card{
  padding: 2.5em 2.5em 1.625em;
  min-height: 39em;
  position: relative;
  display: flex;
  flex-direction: column;
  &:before{
    clip-path: polygon( 82.055% 6.38%,82.055% 6.38%,81.947% 5.345%,81.635% 4.363%,81.135% 3.448%,80.464% 2.612%,79.64% 1.869%,78.679% 1.231%,77.598% 0.712%,76.415% 0.325%,75.146% 0.083%,73.808% 0%,8.247% 0%,8.247% 0%,6.91% 0.083%,5.641% 0.325%,4.457% 0.712%,3.377% 1.231%,2.416% 1.869%,1.591% 2.612%,0.921% 3.448%,0.42% 4.363%,0.108% 5.345%,0% 6.38%,0% 18.519%,0% 20.311%,0% 93.62%,0% 93.62%,0.108% 94.655%,0.42% 95.637%,0.921% 96.552%,1.591% 97.388%,2.416% 98.131%,3.377% 98.769%,4.457% 99.288%,5.641% 99.675%,6.91% 99.917%,8.247% 100%,91.753% 100%,91.753% 100%,93.09% 99.917%,94.359% 99.675%,95.543% 99.288%,96.623% 98.769%,97.584% 98.131%,98.409% 97.388%,99.079% 96.552%,99.58% 95.637%,99.892% 94.655%,100% 93.62%,100% 24.898%,100% 24.898%,99.892% 23.863%,99.58% 22.882%,99.079% 21.966%,98.409% 21.13%,97.584% 20.387%,96.623% 19.749%,95.543% 19.231%,94.359% 18.844%,93.09% 18.602%,91.753% 18.519%,90.303% 18.519%,90.303% 18.519%,88.965% 18.435%,87.696% 18.193%,86.512% 17.806%,85.432% 17.288%,84.471% 16.65%,83.646% 15.907%,82.976% 15.071%,82.476% 14.155%,82.163% 13.174%,82.055% 12.139%,82.055% 6.38% );
  }
  &._green{
    &:after{
      background: #E3FFCE;
    }
    .exam-group__top{
      color: $green;
    }
    .exam-group__items{
      gap: 1em;
    }
    .exam-group__balls{
      background-color: $green;
    }
  }
  &__top{
    padding-right: 5em;
    position: relative;
    z-index: 2;
    min-height: 8em;
  }
  &__title{
    font-weight: 500;
    line-height: inherit;
    li{
      letter-spacing: -0.035em;
      &:before{
        content: ' ';
        padding: 0 .25em 0 0;
      }
    }
  }
  &__groups{
    position: relative;
    z-index: 2;
    margin-top: 1.5em;
  }
  &__img{
    margin-top: auto;
    margin-left: auto;
    padding-top: 1em;
    position: relative;
    z-index: 2;
    width: 19.375em;
    img{
      width: 100%;
      display: block;

      margin-right: -2.5em;
      margin-bottom: -1.625em;
    }
  }
}

.exam-group{
  &:not(:last-child){
    padding-bottom: 1.875em;
    margin-bottom: 1.5em;
    border-bottom: 2px solid #E3EEFF;
  }
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: $primary;
    font-weight: 500;
  }
  &__title{
    .text18{
      letter-spacing: -0.0225em;
    }
  }
  &__count{
    opacity: 0.69;
  }
  &__items{
    margin-top: 1em;
    @include grid100gap(.25em);
  }
  &__item{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__balls{
    min-width: 4.3125em;
    height: 1.875em;
    padding: 0 .25em;
    background-color: $primary;
    color: #fff;
    font-weight: 700;
    border-radius: 2.34375em;
    @include center;
    span{
      font-size: .9375em;
      line-height: 120%;
    }
  }
}

.education{
  padding: 3.75em 0;
  &__items,
  &__tabs{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em / 3));
    gap: .5em;
    align-items: flex-start;
  }
  &__tabs{
    margin-top: 3.5em;
  }
  &__tab{
    padding-left: 2.25em;
    font-weight: 500;
    @include grid100gap(.25em);
  }
  &__items{
    margin-top: 2em;
  }
}

.education-card{
  padding-top: 4em;
  padding-bottom: 5em;
  position: relative;
  height: 100%;
  &._blue{
    &:before{
      background: linear-gradient(90deg, rgba(0, 121, 193, 0.00) 0.97%, #0079C1 9.57%, rgba(0, 121, 193, 0.00) 97.66%);
    }
    .education-card__sale{
      background-color: $primary;
    }
  }
  &._red{
    &:before{
      background: linear-gradient(90deg, rgba(240, 81, 71, 0.00) 1.25%, #F05147 11.72%, #F05147 51.2%, rgba(240, 81, 71, 0.00) 98.91%);
    }
    .education-card__sale{
      background-color: #F05147;
    }
  }
  &:before{
    content: '';
    position: absolute;
    width: 0.1875em;
    height: 100%;
    top: 0;
    left: 2.25em;
    background: linear-gradient(90deg, rgba(122, 193, 67, 0.01) 1.48%, #7AC143 8.58%, #7AC143 60.65%, rgba(122, 193, 67, 0.00) 98.91%);
  }
  &__item{
    display: flex;
    align-items: flex-start;
    position: relative;
    z-index: 2;
    @include lastM(0 0 2.56em);
  }
  &__sale{
    width: 4.4375em;
    height: 2.5em;
    border-radius: 2.34375em;
    @include center;
    padding: 0 .25em;
    background-color: $green;
    color: #fff;
    font-weight: 500;
    margin-right: .75em;
    flex-shrink: 0;
  }
  &__text{
    padding: .75em .875em;
    border-radius: 0.9375em;
    background: #EEF2F9;
    .text16{
      letter-spacing: -0.02em;
    }
    p{
      line-height: inherit;
      @include lastM(0 0 1em);
    }
  }
}

.reviews{
  padding: 3.75em 0;
}

.reviews-slider{
  margin-top: .25em;
  &__wrap{
    overflow: hidden;
    margin-top: 1.25em;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      width: 50%;
      height: auto;
      padding-right: 1.25em;
    }
    &-nav{
      display: flex;
      justify-content: flex-end;
    }
    &-button{
      width: 2.25em;
      height: 2.25em;
      border-radius: 50%;
      background-color: #E3EEFF;
      margin: 0 0 0 .625em;
      position: static;
      transition: all .3s;
      @include center;
      &:hover{
        background-color: #B2B2B2;
      }
      img{
        width: 1.5em;
      }
    }
  }
}

.review-card{
  padding: 2.5em;
  min-height: 19.875em;
  position: relative;
  &:after{
    height: calc(100% - 1.5em);
  }
  &:before{
    clip-path: polygon( 63.546% 11.792%,63.546% 11.792%,63.453% 9.88%,63.185% 8.065%,62.756% 6.373%,62.181% 4.828%,61.474% 3.454%,60.649% 2.275%,59.722% 1.316%,58.707% 0.601%,57.618% 0.154%,56.471% 0%,7.075% 0%,7.075% 0%,5.928% 0.154%,4.839% 0.601%,3.824% 1.316%,2.897% 2.275%,2.072% 3.454%,1.365% 4.828%,0.79% 6.373%,0.361% 8.065%,0.093% 9.88%,0% 11.792%,0% 26.887%,0% 35.377%,0% 88.208%,0% 88.208%,0.093% 90.12%,0.361% 91.935%,0.79% 93.627%,1.365% 95.172%,2.072% 96.546%,2.897% 97.725%,3.824% 98.684%,4.839% 99.399%,5.928% 99.846%,7.075% 100%,92.925% 100%,92.925% 100%,94.072% 99.846%,95.161% 99.399%,96.176% 98.684%,97.103% 97.725%,97.928% 96.546%,98.635% 95.172%,99.21% 93.627%,99.639% 91.935%,99.907% 90.12%,100% 88.208%,100% 38.679%,100% 38.679%,99.907% 36.767%,99.639% 34.952%,99.21% 33.26%,98.635% 31.715%,97.928% 30.341%,97.103% 29.162%,96.176% 28.203%,95.161% 27.488%,94.072% 27.041%,92.925% 26.887%,70.622% 26.887%,70.622% 26.887%,69.474% 26.733%,68.385% 26.286%,67.37% 25.571%,66.443% 24.612%,65.618% 23.433%,64.911% 22.059%,64.336% 20.514%,63.907% 18.822%,63.639% 17.007%,63.546% 15.094%,63.546% 11.792% );
  }
  &__top{
    position: relative;
    z-index: 2;
    padding-right: 9em;
    display: flex;
    align-items: center;
    font-weight: 500;
  }
  &__avatar{
    width: 3.375em;
    height: 3.375em;
    background-color: $primary;
    border-radius: 50%;
    @include center;
    flex-shrink: 0;
    color: #fff;
    margin-right: .94em;
  }
  &__text{
    position: relative;
    z-index: 2;
    padding: 5em 1.25em 0;
  }
}

.req{
  margin: 3.75em 0;
  padding: 6.25em 0 0;
  color: #fff;
  overflow: hidden;
  &:before{
    background-image: url(../img/req-bg.webp);
  }
  &__bottom{
    margin-top: 9em;
    padding-bottom: 9.75em;
    position: relative;
  }
  &__img{
    width: 37.1875em;
    display: block;
    position: absolute;
    right: -10em;
    bottom: 0;
  }
  &__top{
    max-width: 39em;
  }
}

.feedback{
  max-width: 45em;
  &__text{
    @include grid100gap(1.5em);
    .text28{
      opacity: 0.7;
      line-height: 120%;
    }
  }
  &__nav{
    display: flex;
    margin-top: 2.5em;
    .m-btn{
      margin-right: .625em;
    }
  }
}

.events{
  display: flex;
  justify-content: space-between;
  margin-top: 4.75em;
  max-width: 61em;
}

.event{
  .text28{
    font-weight: 500;
  }
  &__dates{
    display: flex;
    flex-wrap: wrap;
  }
  &__date{
    margin-top: .875em;
    margin-right: .5em;
    padding: 0 1em;
    border-radius: 1.125em;
    background: rgba(255, 255, 255, 0.20);
    height: 1.9375em;
    display: flex;
    align-items: center;
    font-weight: 700;
  }
}


@media screen and (max-width: 992px){
  .career{
    padding: 1.875em 0;
    &__top{
      max-width: 27.75em;
      gap: 1.125em;
    }
    &__items{
      display: flex;
      flex-wrap: wrap;
      margin-top: 1em;
      width: calc(100% + .25em);
      .m-badge{
        margin: .25em .25em 0 0;
      }
    }
    &__nav{
      margin-top: 1.25em;
    }
  }

  .exams{
    padding: 1.875em 0;
    &__grid{
      padding: 1em 0 0;
      grid-template-columns: repeat(2,calc(50% - .3125em));
      gap: 2.5em .625em;
    }
  }

  .exam-card{
    padding: 1.25em 1.5em;
    min-height: 22.6875em;
    &__top{
      min-height: 4.6em;
      padding-right: 3em;
    }
    &__groups{
      margin-top: .75em;
    }
    &__img{
      width: 12.125em;
      img{
        margin-right: -1.875em;
        margin-bottom: -1.25em;
      }
    }
  }

  .exam-group{
    &:not(:last-child){
      padding-bottom: .94em;
      margin-bottom: .94em;
    }
    &__top{
      align-items: unset;
    }
    &__balls span,
    &__title .text18,
    .text15{
      font-size: .75em;
      letter-spacing: unset;
    }
    &__items{
      margin-top: .625em;
      gap: .19em;
    }
    &__balls{
      min-width: 2.75em;
      height: 1.1875em;
      border-radius: 1.25em;
    }
    &__count{
      white-space: nowrap;
    }
  }

  .education{
    padding: 1.875em 0;
    &__tabs{
      margin-top: 2.25em;
    }
    &__tab{
      padding-left: 1.25em;
    }
    &__items{
      margin-top: 1.25em;
    }
  }

  .education-card{
    padding-top: 2.38em;
    padding-bottom: 1.88em;
    &:before{
      left: 1.25em;
      width: .1em;
    }
    &__item{
      @include lastM(0 0 1.06em);
    }
    &__sale{
      width: 2.5em;
      height: 1.4375em;
      border-radius: 1.25em;
      margin-right: .25em;
    }
    &__text{
      padding: .3125em .375em;
      border-radius: 0.5em;
      .text16{
        letter-spacing: -.0125em;
      }
      p{
        @include lastM(0 0 1.5em);
      }
    }
  }

  .reviews{
    padding: 1.875em 0;
  }

  .reviews-slider{
    margin-top: -.75em;
    &__wrap{
      margin-top: .5em;
    }
    .swiper{
      &-container{
        width: calc(100% + .625em);
      }
      &-slide{
        padding-right: .625em;
      }
      &-button{
        width: 1.5em;
        height: 1.5em;
        margin: 0 0 0 .5em;
        img{
          width: 1em;
        }
      }
    }
  }

  .review-card{
    padding: 1.25em;
    min-height: 11.875em;
    &:after{
      height: calc(100% - .75em);
      border-radius: 1.25em;
    }
    &__top{
      padding-right: 5em;
    }
    &__avatar{
      width: 1.75em;
      height: 1.75em;
      margin-right: .5em;
    }
    &__text{
      padding: 1.75em .625em 0;
    }
  }

  .req{
    padding-top: 2.5em;
    margin: 1.875em 0;
    &:before{
      background-image: url(../img/req-bg2.webp);
    }
    &__top{
      max-width: 21em;
    }
    &__bottom{
      margin-top: 5em;
      padding-bottom: 2.5em;
    }
    &__img{
      width: 17em;
      right: 0;
    }
  }

  .events{
    max-width: 37em;
    margin-top: 2.5em;
  }

  .event{
    &__date{
      padding: 0 .5em;
      margin: .5em .375em 0 0;
      height: 1.375em;
    }
  }

  .feedback{
    max-width: 24em;
    &__text{
      gap: .5em;
    }
    &__nav{
      margin-top: 1em;
    }
  }
}


@media screen and (max-width: 576px){
  .career{
    &__top{
      max-width: 18.0625em;
    }
    &__items{
      @include grid100gap(.94em);
      width: 100%;
      .m-badge{
        margin: 0;
        background-color: transparent;
        padding: 0 0 0 1.31em;
        min-height: 1em;
        height: unset;
        background-image: url(../img/point3.svg);
        background-size: 1em auto;
        background-position: left center;
        background-repeat: no-repeat;
      }
    }
    &__nav{
      margin-top: 2.25em;
    }
  }

  .exams{
    .text45{
      max-width: 90%;
    }
    &__grid{
      padding-top: 2.125em;
      grid-template-columns: 100%;
    }
  }

  .education{
    &__tabs{
      display: flex;
      justify-content: space-between;
      gap: unset;
    }
    &__items{
      grid-template-columns: 100%;
    }
    &__tab{
      padding-left: 0;
      background-image: url(../img/point4.svg);
      background-size: 0 auto;
      background-repeat: no-repeat;
      background-position: 0 0;
      &._active{
        padding-left: 1.25em;
        background-size: 1em auto;
      }
      &:not(._active){
        color: #B8B8B8;
        .education__tab-price{
          display: none;
        }
      }
    }
    .tab-content{
      display: none;
      &._active{
        display: block;
      }
    }
  }

  .education-card{
    &__item{
      @include lastM(0 0 1.25em);
    }
    &__text{
      max-width: 13.5em;
      flex-grow: 1;
      br{
        display: none;
      }
    }
  }

  .reviews-slider{
    .swiper{
      &-slide{
        width: 100%;
      }
    }
  }

  .req{
    &:before{
      background-image: url(../img/req-bg3.webp);
    }
    &__img{
      position: static;
      width: 14em;
      margin-left: auto;
      margin-right: -2em;
      margin-top: -1.25em;
    }
    &__bottom{
      padding-bottom: 0;
      margin-top: 3.5em;
    }
  }

  .feedback{
    &__nav{
      //width: 8.8125em;
      @include grid100gap(.5em);
      .m-btn{
        margin: 0;
      }
    }
  }

  .events{
    @include grid100gap(1.75em)
  }
}
