.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    padding: 1em 0;
    text-align: center;
    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog{
        text-align: left;
        vertical-align: middle;
    }

    &__bg {
        background: rgba(#011120, 0.80);
        position: fixed;
        width: 100%;
        height: calc(100 * var(--vh));
        top: 0;
        left: 0;
        display: none;
        z-index: 2;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;

        .modal__bg {
            display: block;
        }
    }

    &__dialog {
        border-radius: 2.5em;
        background: #EEF2F9;
        width: 54.4375em;
        max-width: calc(100% - 8.75em);
        position: relative;
        z-index: 3;
        padding: 5em 4.38em;
        display: inline-block;
        overflow: hidden;
    }
    &__close {
        z-index: 10;
        border-top: 0;
        background-color: transparent;
        width: 2em;
        display: flex;
        align-items: center;
        position: absolute;
        top: 2.5em;
        right: 3.75em;
        svg {
            width: 100%;
            height: auto;
        }
    }
    &__top{
        @include grid100gap(.75em);
        text-align: center;
        color: $primary2;
        margin-bottom: 3.25em;
    }
    &__logo{
        width: 18em;
        margin: 14.25em 0 0 auto;
        img{
            width: 100%;
            display: block;
        }
    }
    &__img{
        pointer-events: none;
        position: absolute;
        right: 0;
        bottom: 0;
        width: 48.9375em;
        max-width: 100%;
        img{
            width: 100%;
            display: block;
        }
        ._view2{
            display: none;
        }
    }
}

.modal-form{
    max-width: 37.875em;
    margin: 0 auto;
    &__fields{
        @include grid100gap(1.25em);
    }
    &__conf{
        margin-top: .875em;
        text-align: center;
        a{
            color: $primary2;
            text-decoration: underline;
        }
    }
    &__nav{
        margin-top: 2.25em;
        display: flex;
        justify-content: center;
    }
}



@media screen and (max-width: 992px){
    .modal{
        &__top{
            .text45{
                font-size: 1.5em;
            }
        }
        &__logo{
            margin-top: 17.12em;
            width: 14.74em;
        }
    }

    .modal-form{
        &__nav{
            .m-btn{
                height: 3.1875em;
                padding: 0 2.84em;
                border-radius: 1.875em;
                span{
                    font-size: 1em;
                }
            }
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        padding-top: .5em;
        &:after{
            display: none;
        }
        &__dialog{
            padding: 3.5em 1.25em 2.5em;
            margin: 0 auto;
            max-width: calc(100% - 1.25em);
        }
        &__close{
            top: 1.25em;
            right: 1.25em;
            width: 1.5em;
        }
        &__top{
            margin-bottom: 2.25em;
        }
        &__img{
            ._view1{
                display: none;
            }
            ._view2{
                display: block;
            }
        }
        &__logo{
            width: 8.3em;
            margin-top: 7.25em;
        }
    }
}
