@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2.84em;
    border-radius: 1.875em;
    height: 3.1875em;
    font-weight: 700;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1em;
        line-height: 120%;
    }
}

.m-btn-blue{
    color: #fff;
    background-color: $primary2;
    &:hover{
        background-color: #005096;
        color: #fff;
    }
}

.m-btn-green{
    color: #fff;
    background-color: $green;
    &:hover{
        background-color: #6AAE35;
        color: #fff;
    }
}

.m-btn-white{
    background-color: #fff;
    color: #005BAA;
    &:hover{
        background-color: #E9E9E9;
        color: #005BAA;
    }
}

.m-btn-white-outline{
    border: 2px solid #fff;
    color: #fff;
    &:hover{
        border-color: #D7D7D7;
        color: #D7D7D7;
    }
}


@media screen and (max-width: 992px){
    .m-btn{
        padding: 0 2em;
        height: 2.25em;
        border-radius: 1em;
        span{
            font-size: .875em;
        }
    }
}
